<template>
  <div :ref="refName" :style="{ width: '100%', height: '100%' }"></div>
</template>
<script>
import { getStore } from "@/utils";
export default {
  props: {
    refName: {
      type: String,
      default: "pieCharItem"
    },
    opt: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  data() {
    return {
      getStore,
      poeData: []
    };
  },
  created() {
    console.log(this.refName);
    this.initWave(this.pot, this.refName);
  },
  methods: {
    cpyObj(val) {
      const strJson = JSON.stringify(val);
      return JSON.parse(strJson);
    },
    initWave(opt, refName) {
      let _this = this;
      const dataList = this.cpyObj(this.getStore("tableData").slice(0, 10));
      let totalPrice = 0;
      dataList.forEach(item => {
        totalPrice += parseInt(item.PRICE, 10);
        item.value = item.PRICE;
      });
      const m2R2Data = dataList;
      // const m2R2Data = dataList.map(item => {
      //   const obj = {
      //     value: item.PRICE
      //   };
      //   return { ...obj, ...item };
      // });
      console.log(m2R2Data);
      setTimeout(() => {
        _this.chart = this.$echarts.init(_this.$refs[refName]);
        // 把配置和数据放这里
        _this.chart.setOption({
          title: [
            {
              text: "$" + totalPrice,
              subtext: "total price",
              textStyle: {
                fontSize: 18,
                color: "black"
              },
              subtextStyle: {
                fontSize: 16,
                color: "black"
              },
              textAlign: "center",
              x: "34.5%",
              y: "40%"
            }
          ],
          tooltip: {
            trigger: "item",
            formatter: function(parms) {
              var str =
                parms.data.NAME +
                "</br>" +
                "Counts：" +
                parms.data.COUNT +
                "</br>" +
                "Amount：$" +
                parms.data.PRICE +
                "</br>" +
                "Proportion：" +
                parms.percent +
                "%";
              return str;
            }
          },
