            {
              name: "",
              type: "pie",
              center: ["35%", "50%"],
              radius: ["40%", "65%"],
              clockwise: false, //饼图的扇区是否是顺时针排布
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: "outter",
                  formatter: function(parms) {
                    return parms.data.legendname;
                  }
                }
              },
              labelLine: {
                normal: {
                  length: 5,
                  length2: 3,
                  smooth: true
                }
