<template>
  <el-container :style="{ height: '100%' }">
    <el-aside :style="{ width: '240px', height: '100%' }">
      <div class="aside-center">
        <h3>Chart type</h3>
        <draggable
          class="dragArea list-group"
          :list="list1"
          :group="{ name: 'comp', pull: 'clone', put: false }"
          @change="log"
        >
          <div
            class="list-group-item"
            v-for="element in list1"
            :key="element.id"
          >
            {{ element.name }}
          </div>
        </draggable>
      </div>
    </el-aside>
    <el-container>
      <el-main>
        <div class="upload-file">
          Data source
          <div class="file">
            <i class="el-icon-upload2"></i>
            click top upload data source
            <input
              type="file"
              multiple="multiple"
              @change="uploadCert($event)"
              name="application_files"
            />
          </div>
          <span title="application_files">
            {{ fileName }}
          </span>
        </div>

        <draggable
          tag="el-collapse"
          class="dragArea"
          :list="list2"
          group="comp"
          @change="log"
        >
          <div
            class="item"
            v-for="(item, index) in list2"
            :key="'item_' + index"
          >
            <span class="close-btn" title="delete">
              <i class="el-icon-delete" @click="deleteItem(index)"></i>
            </span>
            <el-table
              v-if="item.type === 'table' && tableData.length"
              :data="tableData"
              border
              height="260"
              row-key="id"
              :header-cell-style="{ background: '#007373', color: '#fff' }"
              style="width: 100%;height:320px"
              default-expand-all
              :tree-props="{
                children: 'childrens',
                hasChildren: 'hasChildren'
              }"
            >
              <template v-for="(items, key) in tableTitle">
                <el-table-column
                  :width="key === 0 ? '180' : ''"
                  :prop="items"
                  :label="key === 0 ? '' : items"
                  :key="'key_' + key"
                >
                </el-table-column>
              </template>
            </el-table>

            <linesItem
              v-if="item.type === 'line'"
              :refName="item.ref + '_' + index"
              :opt="item"
            ></linesItem>
            <pieItem
              v-if="item.type === 'pie'"
              :refName="item.ref + '_' + index"
              :opt="item"
            ></pieItem>
            <barItem
              v-if="item.type === 'bar'"
              :refName="item.ref + '_' + index"
              :opt="item"
            ></barItem>
          </div>
        </draggable>
      </el-main>
    </el-container>
    <el-aside :style="{ width: '210px', height: '100%' }">
      <div class="aside-center"></div>
    </el-aside>
  </el-container>
</template>

<script>
import draggable from "vuedraggable";
import { setStore, getStore, removeStore } from "@/utils";
import linesItem from "@/components/chart/line.vue";
import pieItem from "@/components/chart/pie.vue";
import barItem from "@/components/chart/bar.vue";
export default {
  name: "clone",
  display: "Clone",
  order: 2,
  components: {
    draggable,
    linesItem,
    pieItem,
    barItem
  },
  data() {
    return {
      setStore,
      getStore,
      removeStore,
      fileName: null,
      list1: [
        {
          name: "Line chart",
          id: 1,
          type: "line",
          content: "content...",
          ref: "lineChart"
        },
        {
          name: "Pie chart",
          id: 2,
          type: "pie",
          content: "content。。。。",
          ref: "pieChart"
        },
        {
          name: "Histogram",
          id: 3,
          type: "bar",
          content: "content",
          ref: "barChar"
        },
        {
          name: "Pivot Table",
          id: 4,
          type: "table",
          content: "content",
          ref: "pivotTable"
        }
      ],
      list2: [],
      tableTitle: ["BRAND", "COUNT", "PRICE"],
      dataList: [],
      tableData: [],
      activeNames: [],
      count: 0
    };
  },
  created() {
    // this.tableTitle = await this.getStore("tableTitle").slice(0, 9);
    this.dataList = this.getStore("tableData");

    if (this.dataList) {
      this.tableInit();
    }
    // console.log(this.dataList);
  },
  methods: {
    cpyObj(val) {
      const strJson = JSON.stringify(val);
      return JSON.parse(strJson);
    },
    tableInit() {
      let arryDatas = [];
      // 品牌、BRAND
      this.dataList.forEach((val, i) => {
        let flag = true;
        let { TYPE, NAME, COUNT, PRICE } = val;
        let item = {
          id: i,
          BRAND: NAME,
          COUNT: COUNT,
          PRICE: PRICE
        };
        arryDatas.forEach((el, index) => {
          if (val.BRAND === el.BRAND) {
            flag = false;
            if (val.TYPE === "SUV") {
              arryDatas[index].childrens[0].childrens.push(this.cpyObj(item));
            }
            if (val.TYPE === "Car") {
              arryDatas[index].childrens[1].childrens.push(this.cpyObj(item));
            }
          }
        });
        if (flag) {
          let itemPrent = {
            id: val.BRAND,
            BRAND: val.BRAND,
            childrens: [
              {
                id: "SUV" + i,
                BRAND: "SUV",
                childrens: []
              },
              {
                id: "Car" + i,
                BRAND: "Car",

                childrens: []
              }
            ]
          };

          if (TYPE === "SUV") {
            itemPrent.childrens[0].childrens.push(this.cpyObj(item));
          }
          if (TYPE === "Car") {
            itemPrent.childrens[1].childrens.push(this.cpyObj(item));
          }

          arryDatas.push(itemPrent);
          console.log(itemPrent);
        }
      });

      this.tableData = arryDatas;
      console.log(this.tableData, "----- arryData -----");
    },
    uploadCert(el) {
      const _this = this;
      const files = el.currentTarget.files;
      this.$papa.parse(files[0], {
        download: true, //
        complete: function(results) {
          const data = results.data;
          let arryData = [];
          const titleArry = data[0];
          // _this.tableTitle = titleArry;
          _this.removeStore("tableTitle");
          _this.setStore("tableTitle", titleArry);
          for (let index = 1; index < data.length; index++) {
            let itemArr = data[index];
            let objData = {};
            itemArr.forEach((val, i) => {
              objData[titleArry[i]] = val;
            });
            arryData.push(objData);
          }
          _this.dataList = arryData.slice(0, 50);
          _this.removeStore("tableData");
          _this.setStore("tableData", _this.dataList);
          console.log(arryData, "----- arryData -----");

          _this.tableInit();
        }
      });
    },
    // 上传文件
    uploadCerts(el) {
      const _this = this;
      const files = el.currentTarget.files;
      this.fileName = files[0];
      var reader = new FileReader();
      reader.readAsText(files[0]);
      reader.onload = function() {
        var data = _this.csvToObject(reader.result);
        console.log(data); //data为csv转换后的对象
      };
    },
    csvToObject(csvString) {
      console.log(csvString, "------");
      var csvarry = csvString.split("\r\n");
      var datas = [];
      var headers = csvarry[0].split(",");
      for (var i = 1; i < csvarry.length; i++) {
        var data = {};
        var temp = csvarry[i].split(",");
        for (var j = 0; j < temp.length; j++) {
          data[headers[j]] = temp[j];
        }
        datas.push(data);
      }
      return datas;
    },

    log: function(evt) {
      if (evt.added) {
        if (this.dataList) {
          this.count += 1;
          const item = evt.added.element;
          const idx = this.list2.findIndex(e => e.id === item.id);
          let temp = JSON.parse(JSON.stringify(this.list2));
          temp[idx].id = this.count;
          this.list2 = temp;
        } else {
          this.list2 = [];
          this.$message({
            type: "info",
            message: "Please upload data"
          });
        }
      }
    },
    handleChange: function() {},
    deleteItem: function(index) {
      this.$confirm("Whether to confirm deletion？", "tips", {
        cancelButtonText: "cancel",
        confirmButtonText: "confirm"
      })
        .then(() => {
          this.list2.splice(index, 1);
        })
        .catch(erry => {
          console.log(erry, "-------");
        });
    }
  }
};
</script>
